<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ $store.state.cartProducts.length }} товара
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">

            <div v-show="cartLoading">
              <div class="cart_preloader"></div>
            </div>

            <div v-show="cartLoadingFailed">
              Произошла ошибка при загрузке товаров!
            </div>

            <div v-show="!cartLoading">
              <CartItem v-for="item in products" :key="item.productId" :item="item" />
            </div>
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice | numberFormat }} ₽</span>
          </p>

          <router-link tag="button" :to="{name: 'order'}" v-show="products.length !==0"  class="cart__button button button--primery" type="submit">
            Оформить заказ
          </router-link>

        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapGetters } from 'vuex';
import CartItem from "@/components/CartItem";

export default {
  data() {
    return {
      cartLoading: false,
      cartLoadingFailed: false
    };
  },
  filters: { numberFormat },
  components: { CartItem },
  computed: {
    ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice' }),
  },
  methods: {
    cartLoadProducts() {
      this.cartLoading = true;
      this.cartLoadingFailed = false;
    },
    created() {
      this.cartLoadProducts();
    }
  }
}
</script>
