<template>
  <div class="item__row">
    <div class="form__counter">
      <button type="button" @click.prevent="productMinus" aria-label="Убрать один товар">
        <svg width="12" height="12" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <input type="text" v-model.number="productAmount" :amount="productAmount">

      <button type="button" @click.prevent="productPlus" aria-label="Добавить один товар">
        <svg width="12" height="12" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>


    <button class="button button--primery" type="submit" >
      В корзину
    </button>
     </div>

</template>

<script>
export default {
  props: ['amount'],
  data() {
    return {
      productAmount: 1
    };
  },
  model: {
    prop: 'productAmount',
    event: 'changeAmount',
  },
  methods: {

    productMinus() {
      if (this.productAmount > 1) {
        this.$emit('changeAmount', this.productAmount -= 1);
      }
    },
    productPlus() {
      this.$emit('changeAmount', this.productAmount += 1);
    }
  }
}
</script>
